<template>
  <div class="select-time">
    <Sticky :offset-top="46">
    <tabs @click="selectDay" line-width="88" :ellipsis= 'ellipsis' >
      <tab v-for="(item,index) in days" :key="index" :title='item.title' :cc="item.day"  >
      </tab>
    </tabs>
    </Sticky> 
  </div>
</template>

<script>
import { getToday, formatTime } from "utils/util.js";
import { Tab,Tabs,Sticky  } from 'vant';
export default {
  components:{
    Tab,
    Tabs,
    Sticky 
  },
  props: {
    /**
     * 
     */
    startTime: {
      type: String,
      default: getToday()
    },
    days: {
      type: Array,
      default: null
    },
    day: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      selectedDay: "",
      ellipsis:false
    };
  },
  mounted() {
    this.getWeek(this.startTime);
  },
  // 
  watch: {
    days:function(){
      this.selectDay(0)
    }
  },
  methods: {
    // 7
    getWeek(startTime) {
      const todayTomorrow = ["", "", ""];
      const week = ["", "", "", "", "", "", ""];
      let days = [];
      //
      let start = getToday();
      if (startTime > start) {
        start = startTime;
      }
      for (let i = 0; i < 7; i++) {
        let day = new Date(start);
        day.setDate(day.getDate() + i); //
        const num = (day - new Date(getToday())) / (1000 * 60 * 60 * 24); //00
        days.push({
          title: `${todayTomorrow[num] || week[day.getDay()]}${day.getMonth() +
            1}${day.getDate()}`, // 91 
          day: formatTime(day).split(" ")[0]
        });
      }
      this.$emit("changeDays", days);
    },
    selectDay(name,title) {
      // 
      // 
      const item = this.days[name].day
      const day = item || this.findDefaultDay() || this.days[0].day;
      if (day === this.selectedDay) {
        return;
      }
      this.selectedDay = day;
      this.$emit("selectDayEvent", {
        day
      });
    },
    // 
    findDefaultDay() {
      if (this.days) {
        const day = this.days.find(item => item.day == this.day);
        return day && day.day;
      }
    }
  }
};
</script>

